<template>
    <div class="work_box">
        <a id="work"></a>
        <h1>Work</h1>
        <div class="work_container">
            <div v-for="(vo ,index) in workList" 
            :key="index"  
            class="work" 
            :id="['work_'+ parseInt(index + 1)]"
            @click ="forward(vo)"
            >
            </div>
	    </div>
    </div>
</template>
    
<script>
export default {
    name:'work',
    data(){
        return{
            workList:[
                'http://140.115.236.72/demo-personal/AD103/web/C1600429/index.php',
                'http://140.115.236.72/demo-projects/AD103/AD103G2/gametopia/index.php',
                'https://changchiao.github.io/#/',
                'http://codepen.io/t7552175/pen/mAGXKZ',
                'http://codepen.io/t7552175/pen/mAvmBK',
                'http://codepen.io/t7552175/pen/vybdpq',
            ]
        }
    },
    methods:{
        forward(vo){
            window.location.href = vo;
        }
    },
}
</script>

<style lang="scss">
    @import '~@/assets/css/base.scss';
    .bg{
        @include bg('03');
    }
    .work_box{
      width: 60%;
      margin: 0 auto;
      @include phone-width{
        width: 90%;
      }
      h1{
        @include title;
        text-align:center;
        margin-top: 60px;
        @include phone-width{
            font-size: 28px;
        }
      }
    }
    .work_container {
        padding: 40px 0;
        margin: 0 auto;
        // background-color: #ddd;
        display: flex;
        justify-content: space-around;
        flex-wrap:wrap;

    }

#work_link + h1 {
  font-size: 2rem;
  letter-spacing: .5rem;
  color: #555;
  padding: 40px 0;
  text-align: center;
}

.work {
  height: 200px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
  width: 300px;
  /*margin:0 20px; */
  display: inline-flex;
  position: relative;
  overflow: hidden;
  margin: 20px 0;
  cursor: pointer;
  background-position: center;
  background-size: cover;
  i {
    text-align: center;
    vertical-align: middle;
  }
}

#work_1 {
  background-image: url("../assets/image/work/santax.png");
  &:after {
    content: "SANTAX 森塔斯";
    background-color: rgba(47, 134, 186, 0.8);
  }
}

#work_2 {
  background-image: url("../assets/image/work/screen.png");
  &:after {
    content: " 遊戲烏托邦";
    background-color: rgba(87, 168, 164, 0.8);
  }
}

.work {
  &:after {
    font-size: 1.5rem;
    padding: 20px;
    box-sizing: border-box;
    text-align: left;
    display: block;
    color: #fff;
    letter-spacing: 5px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: .5s;
  }
  &:hover:after {
    left: -100%;
  }
}


#work_3 {
  background-image: url(../assets/image/work/vue_cli.png);
  &:after {
    content: "vue 實作";
    background-color: rgba(46, 163, 90, 0.5);
  }
}


#work_4 {
  background-image: url(../assets/image/work/weather-box.png);
  &:after {
    content: "天氣盒";
    background-color: rgba(46, 163, 90, 0.5);
  }
}

#work_5 {
  background-image: url(../assets/image/work/faces.png);
  &:after {
    content: "canvas表情";
    background-color: rgba(227, 162, 37, 0.49804);
  }
}

// #work_6 {
//   background-image: url(../assets/image/work/3d_menu.png);
//   &:after {
//     content: "3D選單";
//     background-color: rgba(236, 127, 30, 0.5);
//   }
// }

#work_6 {
  background-image: url(../assets/image/work/d3.png);
  &:after {
    content: "D3圖表";
    background-color:rgba(236, 127, 30, 0.5);
  }
}

</style>



