<template>
<div>
    <a id="about"></a>
    <div>   
    <div class="cv_container">
        <h1>About me</h1>
		<div class="pic">
			<img src="../assets/image/work/self.jpg">
		</div>
		<div class="introduce">
			<h2>學歷</h2>
				<ul>
                    <li>輔仁大學影像傳播學系</li>
					<li>財團法人資訊工業策進會 Web/APP前端設計工程師養成班</li>
				</ul>	
            <h2>經歷</h2>	
                <ul>
                    <li>博發科技前端工程師</li>
                </ul>    

			<h2>得獎經歷</h2>	
				<ul>
					<li>資策會個人網站 最佳網站</li>
					<li>關渡電影節 台北影視學校交流入選</li>
					<li>clip studio paint ex 亞洲區繪圖競賽 第十名</li>
					<li>青春有影 2015大學盃 百大精選獎</li>
				</ul>
            <h2>關於我</h2>
                <ul>
                    <li>
                        前端技術推陳出新，期許自己能越戰越勇
                    </li>
                </ul>    
		    </div>
	        </div>	
        </div>
        <comp-mobile v-if="mobileShow"></comp-mobile>
    </div>
</template>

<script>
import $ from "jquery";
import compMobile from '@/components/mobile'
export default {
    name:'information',
    data(){
        return{
            mobileShow:false
        }
    },
    components:{
        compMobile,
    },
    methods:{
    detect(){
        let window_w = $(window).width();
        let window_h = $(window).height();
        if(window_w<768){
            this.mobileShow = true;
        }else{
            this.mobileShow = false;
        }
    }
    },
    mounted(){
        this.detect();    
    },
    created(){
        // window.addEventListener('mousemove',this.detect);
    }
}
</script>

<style lang="scss" >
    @import '~@/assets/css/base.scss';
    #main_section{
        background-color: #282f44;
    }
    .bg{
        @include bg('01');
    }

    .cv_container {
        padding: 50px;
        text-align: center;
        margin: 0 auto;
        width: 500px;
        @include phone-width{
            width: 100%;
            padding: 0px;
            padding-top: 50px;
        }
        h1{
            @include title;
            @include phone-width{
                text-align: center;
                font-size: 28px;
            }
        }
    }

    .pic {
        vertical-align: top;
        width: 30%;
        @include phone-width{
            width: 100%;
            text-align: center;
        }
        img {
            width: 100px;
            height: 100px;
            object-fit: cover;
            object-position: center;
            border-radius: 50%;
        }
    }

    .introduce {
        vertical-align: top;
        display: inline-block;
        width: 70%;
        font-size: 1rem;
        color: #333;
        text-align: left;
        box-sizing: border-box;
        padding: 30px 0px 30px 30px;
        color: #777;
        line-height: 2rem;
        border-left: 2px dashed #ccc;
        ul {
            padding-left: 25px;
            @include phone-width{
                padding: 0px;
            }
        }
        h2 {
            padding: 10px;
            letter-spacing: 0.5rem;
            color: #333;
        }
        span {
            font-size: 1.1rem;
            display: block;
            padding: 10px 0 0 20px;
            color: #555;
            font-weight: 400;
            letter-spacing: 5px;
            + p {
            padding: 10px 0 0 40px;
            }
        }

        // @media screen and (max-width: 768px){
        //     .cv_container {
        //         padding: 10px;
        //         text-align: center;
        //         width: 100%;
        //         background: #aaa;
        //     }
        // }

}

</style>


