<template>
<div class="skill_box">
    <a id="skill"></a>
    <h1>Skill</h1>
    <div class="skill_container">
       <div class="skill" v-for="(vo ,index) in skillList" :key="index">
           <img :src="vo.img" alt="">
           <span>{{vo.text}}</span>
       </div>
    </div>
</div>    
</template>

<script>
import img1 from '../assets/image/html.png'
import img2 from '../assets/image/css.png'
import img3 from '../assets/image/sass.png'
import img4 from '../assets/image/js.png'
import img5 from '../assets/image/bootstrap.png'
import img6 from '../assets/image/vue.png'

export default {
  name:'skill',
  data(){
      return{
          skillList:[
              {img:img1,text:'HTML'},
              {img:img2,text:'CSS'},
              {img:img3,text:'SASS'},
              {img:img4,text:'JAVASCRIPT'},
              {img:img5,text:'BOOTSTRAP'},
              {img:img6,text:'VUE'},
          ],
      }
  }
}
</script>

<style lang="scss" scoped>
    @import '~@/assets/css/base.scss';
    #main_section{
        background-color: #453329;
    }
    .bg{
        @include bg('02');
    }
    .skill_box{
        width: 50%;
        margin:  0 auto;
         @include phone-width{
            width: 90%;
            margin-bottom: 10px;
        }
    }
    h1{
       @include title;
        text-align:center;
        margin-top: 60px;
        @include phone-width{
            font-size: 28px;
        }
    }
    .skill_container{
        padding: 80px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        .skill{
            width:30%;
            margin-bottom: 30px;
            @include phone-width{
                width: 50%;
                margin-bottom: 10px;
                text-align: center;
            }
            img{
                width: auto;
                height: 80px;
                vertical-align: middle;
                @include phone-width{
                    text-align: center;
                }
            }
            span{
                vertical-align: middle;
                font-weight: 800;
                padding-left:30px;
                @include phone-width{
                    padding: 0;
                    display: block;
                }
            }
            &:last-child span{
                padding-left:60px;
                @include phone-width{
                    padding-left:0px;
                }
            }
        }
    }
</style>


