<template>
    <header>
        <!-- <img src="/src/assets/image/image/logo.png" alt="">     -->
        <ul>
            <li v-for="(vo ,index) in menuList" :key="index">
            <router-link :to="vo.href">
                <i :class="['fa', vo.fa]" aria-hidden="true" ></i>  
                <p>{{vo.textCH}}</p>
                <span>{{vo.textEN}}</span>
            </router-link>
            </li>
        </ul>
        <div id="main_section">
			<div id="fish"></div>
			<h1>Chang <i class="fa fa-handshake-o" aria-hidden="true"></i>
			 ChiaoTing</h1>
			
			<h3>Front-End Engineer</h3>
			<img id="cat" :src="catStage" alt="">
			
		</div>
        <!-- <div><h1>Joe Chang profolie</h1></div> -->
        
    </header>
</template>

<script>
import $ from "jquery";
import img1 from '../../assets/image/cat-left.png';
import img2 from '../../assets/image/cat-right.png';
import img3 from '../../assets/image/cat-center.png';
import img4 from '../../assets/image/cat-left_top.png';
import img5 from '../../assets/image/cat-right_top.png';

export default {
  name:'headTop',
  data(){
      return{
          menuList:[
              {href:"/",textCH:'首頁',textEN:'HOME',fa:'fa-home'},
              {href:"/work",textCH:'作品',textEN:'WORK',fa:'fa-check'},
              {href:"/skill",textCH:'技能',textEN:'SKILL',fa:'fa-bolt'},
              {href:"/contact",textCH:'聯絡',textEN:'contact',fa:'fa-paper-plane'},
          ],
          cat:[
              img1,img2,img3,img4,img5
          ],
          catStage:img1,
          iconShow:false,
      } 
  },
  computed:{

  },
  methods:{
      catStageFun(event){

        let x = event.pageX;
        let y = event.pageY;  

        let window_w = $(window).width();
        let window_h = $(window).height();

        $("#fish").css("left", x+"px");
        $("#fish").css("top", y-30+"px"); 
         
        if(x< window_w/2-50){
            this.catStage =  this.cat[0];
        }else if(x>window_w/2 + 50){
            this.catStage =  this.cat[1];
        }else{
            this.catStage =  this.cat[2];
        }
        if(x<window_w/2-50 && y<window_h/2){
            this.catStage =  this.cat[3];	
        }
        if(x>window_w/2+50 && y<window_h/2){
            this.catStage =  this.cat[4];
        }
      },
      iconCtrl(){
            this.iconShow = false;
        $('ul a').mouseenter(function() {
            this.iconShow = !this.iconShow;
            // console.log(this.iconShow);
        });
      }
  },mounted(){

  },created(){
       window.addEventListener('mousemove',this.catStageFun);
    //    window.addEventListener('mousemove',this.iconCtrl);
  }
}
</script>

<style lang="scss">
    @import '~@/assets/css/base.scss';      
    header{
        text-align: center;
        position: relative;
        z-index: 999;
        image{
            
        }
        h1{
            margin: 60px 0  30px 0;
            font-size: 42px;
            font-weight: 900;
            color: $base_color;
        }
        ul{
            width: 600px;
            margin: 0 auto;
            height: 50px;    
            position: absolute;
            top:30px;
            left: 0;
            right: 0;
            z-index:999;
            display: flex;
            align-items: center;
            justify-content: space-between;
            li{
                a{
                    font-family: $base_font;
                    color:$base_color;
                    letter-spacing: 4px;
                    span{
                        display: inline-block;
                        margin: 10px 0;
                        font-size: 12px;
                        color:$light;
                        letter-spacing: 2px;
                    }
                    p{
                        display: inline-block;
                        padding-top: 10px;
                    }
                    &:hover, &:hover span{
                        color: #ffaa00;
                    }
                }
            }
        }
    }
    #main_section {
        padding: 30px;
        background-color: #282f44;
        box-sizing: border-box;
        text-align: center;
        color: #fff;
        position: relative;
        height: 500px;
        overflow: hidden;
            h1 {
                padding-top: 30px;
                font-size: 32px;
                letter-spacing: 2px;
            }
            h3 {
                padding-top: 20px;
                font-weight: 200;
                color: #ddd;
                letter-spacing: 2px;
            }
            i {
                color: orange;
            }
        }

        #work_section {
            text-align: center;
            padding-bottom: 30px;
        }

        #cat {
            position: absolute;
            height: 300px;
            bottom: -5px;
            width: 400px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 100;
        }

        #fish {
        position: absolute;
        &:after {
            content: "";
            position: absolute;
            display: block;
            height: 20px;
            width: 20px;
            border: 3px solid #fff;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%) rotate(45deg);
        }
}
</style>



