<template>
  <div id="app">
    <comp-headTop v-if="!mobileShow"></comp-headTop>
    <router-view/>
    <comp-footer></comp-footer>
    <div class="top" v-if="!mobileShow" @click="goTop">
      <i class="fa fa-angle-up fa-3x" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import compHeadTop from '@/components/common/headTop'
import compFooter from '@/components/common/footer'
export default {
  name: 'App',
  data(){
    return{
      mobileShow:false
    }
  },
  components:{
    compHeadTop,
    compFooter
  },
  methods:{
    detect(){
       let window_w = $(window).width();
       let window_h = $(window).height();
       if(window_w<768){
         this.mobileShow = true;
       }else{
         this.mobileShow = false;
       }
    },
    topShow(){
       $(window).scroll(function() {
          if ( $(this).scrollTop() > 100){
              $('.top').addClass('on');
          } else {
              $('.top').removeClass('on');
          }
      });
    },
    goTop(){
      jQuery("html,body").animate({
            scrollTop:0
      },800);
    }
  },
  mounted(){
      this.detect();
  },created(){
       window.addEventListener('resize',this.detect);
       window.addEventListener('scroll',this.topShow);
  }
}
</script>

<style lang="scss">
@import '~@/assets/css/reset.scss';
@import '~@/assets/css/base.scss';

.top{
  position: fixed;
  bottom: 10px;
  right:10px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: #555;
  color: #fff;
  display: none;
  transition-duration: .5s;
}
.on{
  display: block;
}
</style>



