<template>
  <div class="mobile">
      <!-- <comp-information></comp-information> -->
      <div class="mobileTop">    
        <span>Chang Chiao Ting</span>
        <i @click="showList" :class="['fa fa-2x' , icon ]" aria-hidden="true"></i>
       
      </div>
      <div class="mobile_list">
          <ul>
              <li><a @click="hidden" href="#about">About<span>關於</span></a></li>
              <li><a @click="hidden" href="#skill">Skill<span>技能</span></a></li>
              <li><a @click="hidden" href="#work">Work<span>作品</span></a></li>    
              <li><a @click="hidden" href="#contact">Contact<span>聯絡</span></a></li>
          </ul>
      </div>
      <comp-skill></comp-skill>
      <comp-work></comp-work>
      <comp-contact></comp-contact>
  </div>
</template>

<script>
import $ from "jquery";
import compInformation from '@/components/information'
import compSkill from '@/components/skill'
import compWork from '@/components/work'
import compContact from '@/components/contact'
export default {
  name:'mobile',
  data(){
      return{
          icon:'fa-bars',
          close:false,
      }
  },
  components:{
      compInformation,
      compSkill,
      compWork,
      compContact
  },methods:{
      showList(){
          $('.mobile_list').toggleClass('show');
              if(this.close){
                 this.icon ='fa-times';
                 this.close = !this.close;
              }else if(!this.close){
                 this.icon = 'fa-bars';
                 this.close = !this.close;
              }
      },
      hidden(){
          $('.mobile_list').removeClass('show');
      }
  }
}
</script>
<style lang="scss">
@import '~@/assets/css/base.scss';
    .mobileTop{
        text-align: center;
        color: #fff;
        background: steelblue;
        position: fixed;
        width: 100%;
        line-height: 50px;
        height: 50px;
        top: 0;
        z-index:999999;
        i.fa-bars ,i.fa-times{
            position: absolute;
            left: 10px;
            top:8px;
            cursor: pointer;
        }
    }

    .mobile_list{
        width: 100%;
        height: 100vh;
        overflow: hidden;
        background: rgba(64, 89, 143, 0.8);
        position: fixed;
        top:50px;    
        transition-duration: .5s;
        left:-100%;
        color:#fff;
        z-index: 99999;
        text-align: center;
        ul{
            width: 80%;
            margin: 0 auto;
            padding: 100px 0px;
            li{
                display: block;
                padding: 20px 0;
                a{
                    font-size: 14px;
                    color:#ddd;
                }
                span{
                   color: #fff;
                   padding-left: 10px;     
                }
            }
        }
    }
    .show{
        left: 0;
    }
</style>



